import React, { useEffect } from 'react';
import { Link } from 'gatsby';

import logoWhite from '../images/nav/logo-white.svg'
import constants from '../constants/footer';
import ShaderProgram from './utils/ShaderProgram';

const Footer = () => {

  useEffect(() => {
    const pointSize = 2;
    new ShaderProgram(document.querySelector('.particles'), {
      texture: 'data:image/png;base64,',
      uniforms: {
        size: { type: 'float', value: pointSize },
        field: { type: 'vec3', value: [0, 0, 0] },
        speed: { type: 'float', value: 5 },
      },
      vertex: `
        #define M_PI 3.1415926535897932384626433832795

        precision highp float;

        attribute vec4 a_position;
        attribute vec4 a_color;

        uniform float u_time;
        uniform float u_size;
        uniform float u_speed;
        uniform vec3 u_field;
        uniform mat4 u_projection;

        varying vec4 v_color;

        void main() {

          vec3 pos = a_position.xyz;

          pos.y += (
            cos(pos.x / u_field.x * M_PI * 8.0 + u_time * u_speed) +
            sin(pos.z / u_field.z * M_PI * 8.0 + u_time * u_speed)
          ) * u_field.y;

          gl_Position = u_projection * vec4( pos.xyz, a_position.w );
          gl_PointSize = ( u_size / gl_Position.w ) * 100.0;

          v_color = a_color;

        }`,
      fragment: `
        precision highp float;

        uniform sampler2D u_texture;

        varying vec4 v_color;

        void main() {

          gl_FragColor = v_color * texture2D(u_texture, gl_PointCoord);

        }`,
      onResize(w, h, dpi) {

        const position = [], color = []

        const width = 400 * (w / h)
        const depth = 400
        const height = 3
        const distance = 5

        for (let x = 0; x < width; x += distance) {
          for (let z = 0; z < depth; z += distance) {

            position.push(- width / 2 + x, -30, -depth / 2 + z)
            color.push(0, 1 - (x / width) * 1, 0.5 + x / width * 0.5, z / depth)

          }
        }

        this.uniforms.field = [width, height, depth]

        this.buffers.position = position
        this.buffers.color = color

        this.uniforms.size = (h / 400) * pointSize * dpi

      },
    });

    return () => { };
  }, []);

  return (
    <>
      <footer>
        {/* Top footer section */}
        <div className='row mx-2 footer-row px-2 px-lg-3 py-3'>
          <div className='waves particles w-100'>
            <div className='row d-flex mx-0 w-100 mt-2 mb-5 px-lg-5 upper-half'>
              <div className='col-md-6 col-xl-3 d-none d-md-block mb-md-5 mb-xl-0 px-lg-5 order-md-0'>
                <h3 className="mt-3 mb-2"></h3>
                <img src={logoWhite} className="img-fluid mb-3" alt="Logo" />
                <p>
                  NetSurf           18,000    .
              </p>
                <Link to='/zashto-netsurf/'> </Link>
              </div>
              <div className='col-12 col-lg-6 col-xl-3 px-lg-5 order-3'>
                <div className='row'>
                  <div className='col-6 m-0 p-0'>
                    <h3 className="my-3"></h3>
                    <Link to='/za-doma/' className='d-block my-2'> </Link>
                    <Link to='/za-biznesa/' className='d-block my-2'> </Link>
                    <Link to='/netsurf-play/' className='d-block my-2'>NetSurf Play</Link>
                  </div>
                  <div className='col-6'>
                    <h3 className="my-3"></h3>
                    <a className='d-block my-2' href="http://forum.net-surf.net/" target="_blank" rel="noopener noreferrer"></a>
                    <Link to='/magazini/' className='d-block my-2'></Link>
                    <Link to='/help-center/' className='d-block my-2'></Link>
                    {/* <Link to='/ustroistva' className='d-block my-3'></Link> */}
                    {/* <Link to='/ustroistva' className='d-block my-3'></Link> */}
                  </div>
                </div>
              </div>
              <div className='col-12 col-lg-6 col-xl-3 px-lg-5 order-3'>
                <h3 className="my-3 text-left">  NetSurf</h3>
                <div className='row p-0 m-0'>
                  <div className='col-6 m-0 p-0'>
                    <a href='https://netsurf.bg/wp-content/uploads/2019/12/Cookies_01_12_2019.pdf' target="_blank" rel="noopener noreferrer" className='d-block my-2'></a>
                    <Link to='/obshti-uslovia/' className='d-block my-2'> </Link>
                    <Link to='/fondaciq-netsurf/' className='d-block my-2'> NetSurf</Link>
                    {/* <Link to='/careers' className='d-block my-2'></Link> */}
                  </div>
                  <div className='col-6'>
                  </div>
                </div>
              </div>
              <div className='col-12 col-md-6 col-xl-3 px-xl-2 text-md-right text-center order-0 order-xl-3'>
                <h3 className="mt-3 mb-2 text-green"></h3>
                <div className='d-inline'>
                  <div className='text-lg-right justify-content-between align-items-center'>
                    <div className='my-2'>
                      <p> :</p>
                      <h2 className=''><a href="tel:088 525 0000">088 525 0000</a></h2>
                    </div>
                    <div className='my-2'>
                      <p> :</p>
                      <h2 className=''><a href="tel:088 299 1611">088 299 1611</a></h2>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Lower footer section */}
            <canvas id="myCanvas" width="100%" style={{maxHeight: '200px'}}>
            </canvas>
            <div className='row text-white d-flex justify-content-center w-100 mt-5 lower-half'>
              <div className='col-12 col-md-5 text-center'>
                <div className='d-md-inline-block'>
                  {constants.socialMediaIcons.map(({ link, icon_class }, index) => (
                    <a className="m-2" key={index} href={link} target="_blank" rel="noopener noreferrer"><span className={'icon ' + icon_class}></span></a>
                  ))}
                </div>
                <div className='mt-4 copyright'>
                  <p>Copyright  NetSurf, {new Date().getFullYear()}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </footer>
    </>
  )
}


export default Footer;