import React, { useReducer } from 'react';
import styled from 'styled-components';
import { Modal } from 'react-bootstrap';
import ModalComponent from './modal';
import { graphql, useStaticQuery } from 'gatsby';


const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bg});
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: 0px 32px 102px rgba(23, 9, 40, 0.36);
  border-radius: 16px;
`;

const Container = styled.div`
  @media screen and (min-width: 1600px) {
    padding: 0rem 4rem 4rem 4rem;
  }
`;

const reducer = (state, action) => {
  switch (action.type) {
    case 'field': {
      return {
        ...state,
        [action.field]: action.value,
      }
    }
    case 'success': {
      return {
        ...state,
        success: true,
      }
    }
    case 'valid_fields': {
      return {
        ...state,
        'nameIsValid': action.nameIsValid,
        'emailIsValid': action.emailIsValid,
        'messageIsValid': action.messageIsValid,
        'telephoneIsValid': action.telephoneIsValid,
        'category': action.category,
      }
    }
    case 'failure': {
      return {
        ...state,
        failure: true,
      }
    }
    default: {
      return state;
    }
  }
}

const initialState = {
  username: '',
  password: '',
  success: false,
  failure: false,
}


export default ({ show, setShow }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  const { username, password } = state;

  const data = useStaticQuery(graphql`
    query LoginForm {
      bg: file(relativePath: {eq: "home/form-background.png"}) {
        childImageSharp {
          fluid(maxWidth: 1000) {
            src
          }
        }
      }
    }
  `);


  return (
    <>
      <ModalComponent
        show={show}
        setShow={setShow}
        dialogClassName="form-dialog"
      >
        <BackgroundWrapper bg={data.bg.childImageSharp.fluid.src}>
          <Modal.Header>
            <Modal.Title id="custom-modal-styling-title" className='w-100'>
              <div className='row w-100 d-flex justify-content-end'>
                <span className='icon popup-close' onClick={() => { setShow(false) }}></span>
              </div>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>

            <Container className='container'>
              <>
                <h2 className='mb-4'>  </h2>

                <form
                  className='popup-form'
                  method="post"
                  // action="https://netsurf.bg/wp-login.php"
                  action="https://netsurf.bg/wp-admin/admin-ajax.php"
                  name="ext_login"
                // name="loginform"
                >
                  {/* onSubmit={onSubmit}> */}
                  <div className="form-row">
                    <div className="form-group col-md-6">
                      <label for="user_login"> </label>
                      <input
                        type="text"
                        className="form-control"
                        id="user_login"
                        // name="log"
                        name="up_username"
                        placeholder="  "
                        value={username}
                        onFocus={(e) => e.target.placeholder = ""}
                        onBlur={(e) => e.target.placeholder = "  "}
                        onChange={(v) => dispatch({
                          type: 'field',
                          field: 'username',
                          fieldValid: 'usernameIsValid',
                          value: v.currentTarget.value,
                        })}
                      />
                      {/* {!nameIsValid && <small className='text-danger'> 5 </small>} */}
                    </div>
                    <div className="form-group col-md-6">
                      <label for="user_pass"></label>
                      <input
                        type="password"
                        className="form-control"
                        id="user_pass"
                        // name="pwd"
                        name="up_password"
                        placeholder="*******"
                        value={password}
                        onFocus={(e) => e.target.placeholder = ""}
                        onBlur={(e) => e.target.placeholder = "*******/*  */"}
                        onChange={(v) => dispatch({
                          type: 'field',
                          field: 'password',
                          fieldValid: 'passwordIsValid',
                          value: v.currentTarget.value,
                        })}
                      />
                      {/* {!emailIsValid && <small className='text-danger'> </small>} */}
                    </div>
                  </div>

                  <div className='w-100 text-center text-sm-right'>
                    <p className='submit'>
                      <input type="submit" value="" className="btn btn-primary green" id="submit-login" name="login" />
                      <input type="hidden" value="https://userportal.net-surf.net" name="redirect_to" />
                      {/* <input type="hidden" value="1" name="testcookie" /> */}

                    </p>
                  </div>
                  {/* {failure && <p className='text-danger'>  !  .</p>} */}
                </form>
              </>
            </Container>
          </Modal.Body>
        </BackgroundWrapper>
      </ModalComponent>
    </>
  )
}