import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useCookies } from 'react-cookie';


const BannerWrapper = styled.div`
  background: #151F41;
  border-radius: 8px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 35%;
  z-index: 9999;
  padding: 0.7rem;
  margin: 0.5rem;

  @media screen and (max-width: 1200px) {
    width: 45%;
  }

  @media screen and (max-width: 576px) {
    width: 80%;
  }
`;

const CloseButton = styled.span`
  position: absolute;
  float: right;
  cursor: pointer;
  right: -0.5rem;
  top: -0.5rem;
`;

export default () => {
  const yearInMilliseconds = 365 * 24 * 60 * 60 * 1000;

  const [show, setShow] = useState(false);
  const [cookies, setCookie, removeCookie] = useCookies(['cookieConsent']);

  useEffect(() => {
    if (!cookies.cookieConsent) {
      setTimeout(() => {
        setShow(true);
      }, 2000);
    }
  });

  const closeConsent = (e) => {
    setCookie('cookieConsent', true, { expires: new Date(Date.now() + yearInMilliseconds) });
    setShow(false);
  }

  return (
    <>
      {show && (
        <BannerWrapper>
          <CloseButton className='icon close-cross' onClick={closeConsent}></CloseButton>
          <h6 className='text-white'>
              
            <a href='https://netsurf.bg/wp-content/uploads/2019/12/Cookies_01_12_2019.pdf' target="_blank" rel="noopener noreferrer"><span className='text-green'> </span></a>
            ,         .</h6>

        </BannerWrapper>
      )}

    </>
  );
}