import React, { useState, useRef, useEffect } from "react";
import { Link } from "gatsby";
import PropTypes from "prop-types"
import Sticky from 'react-stickynode';
import Popup from './shared/popup'

import logo from '../images/nav/logo.svg';
import logoWhite from '../images/nav/logo-white.svg';
import pages from '../constants/pages';
import LoginPopup from "./shared/login-popup";

const Header = ({ activeTab }) => {
  const [isNavbarOpen, setNavbarOpen] = useState(false);
  const [showTelephonePopover, setShowTelephonePopover] = useState(false);
  const target = useRef(null);
  const wrapperRef = useRef(null);


  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    document.addEventListener("mousedown", handleClickOutside);


    return () => {
      window.removeEventListener('scroll', handleScroll);
      document.removeEventListener("mousedown", handleClickOutside);
    }
  })

  const handleClickOutside = (event) => {
    if (wrapperRef.current && !wrapperRef.current.contains(event.target)) {
      setNavbarOpen(false);
    }
  }
  const handleScroll = (e) => {
    setNavbarOpen(false);
  }

  return (
    <header>
      <div className='mx-lg-5'>
        <TopNavigation
          isNavbarOpen={isNavbarOpen}
        />

        <Sticky enable={true} innerZ={50}>
          <MainNavigation
            passRef={wrapperRef}
            isNavbarOpen={isNavbarOpen}
            setNavbarOpen={(v) => setNavbarOpen(v)}
            showTelephonePopover={showTelephonePopover}
            setShowTelephonePopover={(v) => setShowTelephonePopover(v)}
            target={target}
            activeTab={activeTab}
          />
        </Sticky>
      </div>
    </header>
  )
}

const MainNavigation = ({ isNavbarOpen, setNavbarOpen, showTelephonePopover, setShowTelephonePopover, target, activeTab, passRef }) => {
  return (
    <nav
      className={"navbar navbar-dark navbar-expand-lg d-flex justify-content-between main-navbar px-lg-5 " + (isNavbarOpen && "open")}
      ref={passRef}
    >
      <button
        className="navbar-toggler"
        type="button"
        // data-toggle="collapse"
        // data-target=".navbar-collapse"
        onClick={(_) => {
          setNavbarOpen(!isNavbarOpen);
        }}
      >
        {isNavbarOpen ? (
          <span className="icon navbar-toggler-close-icon"></span>
        ) : (
            <span className="navbar-toggler-icon"></span>
          )}
      </button>
      <Logo
        isNavbarOpen={isNavbarOpen}
        logo={logo}
        logoWhite={logoWhite}
      />
      <div className='d-lg-none d-block'>
        <button className='navbar-toggler' ref={target} onClick={(_) => setShowTelephonePopover(!showTelephonePopover)}>
          <span className="icon telephone-toggler-icon"></span>
        </button>
        <Popup
          onHide={() => setShowTelephonePopover(false)}
          showTelephonePopover={showTelephonePopover}
          setShowTelephonePopover={(v) => setShowTelephonePopover(v)}
          target={target}
        >
          <div className='popup d-flex justify-content-end align-items-end py-4 pl-4 pr-3'>
            <div className='text-right'>
              <div className='my-2'>
                <p className='number-blue'> :</p>
                <h2 className='number'><a href="tel:088 525 0000">088 525 0000</a></h2>
              </div>
              <div className='my-2'>
                <p className='number-green'> :</p>
                <h2 className='number'><a href="tel:088 299 1611">088 299 1611</a></h2>
              </div>
            </div>
          </div>
        </Popup>
      </div>
      <div className='d-xl-block d-none order-2'>
        <Link to={"/za-doma/"} className='btn btn-primary green'> </Link>
      </div>
      <div className={"navbar-collapse collapse w-100 order-2 order-md-0 " + (isNavbarOpen && 'show')}>
        <ul className="navbar-nav mx-auto">
          {pages.links.map(({ name, href }, index) => (
            <li key={index} className="nav-item mt-2 mt-lg-3 mx-3">
              <Link to={href} className={"nav-link d-inline-block " + (activeTab === href && " active")}>{name}</Link>
            </li>
          ))}
          <li className="nav-item my-4 mt-lg-3 mx-3">
            {isNavbarOpen && (<Link to='/za-doma/' className='btn btn-primary green'> </Link>)}
          </li>
        </ul>
      </div>
    </nav>
  )
}

const TopNavigation = ({ isNavbarOpen }) => {
  const target = useRef(null);
  const [show, setShow] = useState(false);
  const targetLogin = useRef(null);
  const [isLoginOpen, setLoginOpen] = useState(false);


  return (
    <div className={'top-nav ' + (isNavbarOpen && "open")}>
      <div className='row mx-lg-5 h-100'>
        <div className='col-4 d-none d-lg-flex justify-content-start align-items-center pl-lg-5 h-100 top-left-section'>
        </div>
        <div className='col-lg-3 d-none d-lg-block'></div>
        <div className={'col-12 col-lg-5 d-flex align-self-center ' + (isNavbarOpen ? "justify-content-start" : "justify-content-end")}>
          <div className="d-inline px-3">
            <Link to={"/pokritie/"}></Link>
          </div>
          <div className="topnav-item d-inline px-3">
            <Link to={"/magazini/"}></Link>
          </div>
          <div className="topnav-item pl-2 d-inline-block" ref={target} onClick={(_) => { setShow(!show); }}>
            <span className='icon phone-icon d-none d-lg-inline-block mr-1'></span> <button className='mr-lg-0 d-none d-lg-inline-block'></button>
          </div>
          <Popup
            showTelephonePopover={show}
            setShowTelephonePopover={(v) => setShow(v)}
            target={target}
            onHide={() => setShow(false)}
          >
            <div className='popup d-flex justify-content-end align-items-end py-4 pl-4 pr-3'>
              <div className='text-right justify-content-between'>
                <div className='my-2'>
                  <p className='number-blue'> :</p>
                  <h2 className='number'><a href="tel:088 525 0000">088 525 0000</a></h2>
                </div>
                <div className='my-2'>
                  <p className='number-green'> :</p>
                  <h2 className='number'><a href="tel:088 299 1611">088 299 1611</a></h2>
                </div>
              </div>
            </div>
          </Popup>
          <div className="topnav-item d-inline-block pl-lg-2" ref={targetLogin} onClick={(_) => { setLoginOpen(!isLoginOpen); }}>
            <span className='icon log-in-icon d-none d-lg-inline-block'></span>
            <button className='ml-lg-0 d-inline-block'></button>
          </div>

          <Popup
            showTelephonePopover={isLoginOpen}
            setShowTelephonePopover={(v) => setLoginOpen(v)}
            target={targetLogin}
            onHide={() => setLoginOpen(false)}
          >
            <div className='popup d-flex justify-content-center align-items-center flex-column p-4'>
              <div className='m-4 text-left'>
                <p><span className='icon package pay mr-2'></span></p>
                <p><span className='icon package services mr-2'></span></p>
                <p><span className='icon package cut mr-2'></span>Pause/Cut</p>
                <p><span className='icon package record mr-2'></span></p>
                <p><span className='icon package hbo mr-2'></span>HBO GO</p>
                <p><span className='icon package settings mr-2'></span></p>
                <p><span className='icon package netsurfplay mr-2'></span>NetSurf Play</p>
                <p><span className='icon package promos mr-2'></span></p>
              </div>
              <a
                href="https://userportal.net-surf.net"
                className='btn btn-sm blue text-center'
              ></a>
            </div>
          </Popup>
        </div>
      </div>
    </div>
  )
}

const Logo = ({ isNavbarOpen, logoWhite, logo }) => (
  <Link to={"/"} className={"ml-xl-4 d-block text-center order-0 order-md-0 "}>
    <img src={isNavbarOpen ? logoWhite : logo} className="logo" alt="Logo" />
  </Link>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
